import React, { useEffect, useState } from "react";
import Post from "./Post";
import styled from 'styled-components';



const QnA = () => {

    const [quesAns, setQuesAns] = useState([]);

    useEffect(() => {
        getResponseData();
    }, []);

    const [responseDataLoading, setResponseDataLoading] = useState(false);

    const getResponseData = async () => {
        try {
            setResponseDataLoading(false);
            const response = await fetch("https://recruitingmonk-v2.azurewebsites.net/qna", {
                mode: 'cors',
                headers: {
                    'Access-Control-Allow-Origin': 'https://web-advisor.github.io',
                    'Content-Type':'application/json'
                },
                method:'GET'
            });
            await response.json().then(res => {
                setQuesAns(res);
            });
            setResponseDataLoading(true);
        } catch (e) {
            console.log("Here " + e);
        }
    }

    return (
        <>
            <Feed>
                {
                    responseDataLoading ?
                        quesAns.map((data, i) => {
                            return (
                                <Post
                                    key={i}
                                    data={data}
                                />
                            );
                        }) :
                        <div className="p-5 w-100 h-100 justify-content-center align-content-center d-flex">
                            <div className="spinner-grow" style={{ width: "25em", height: "25em" }} role="status">
                                <span className="visually-hidden">Loading...</span>
                            </div>
                        </div>
                }
            </Feed>
        </>
    );
}

const Feed = styled.main`
    width:65%;
    min-height:100vh;
    margin:20px auto;
    padding-top:10px;
    padding-bottom:10px;
    background-color:#F1F2F2;
    border-radius:10px;
    @media(max-width:768px){
        width:90%;
    }
    @media(max-width:676px){
    }
`;

export default QnA;

