import React, { useEffect } from "react";
import styled from 'styled-components';
import Button from './Button';
import Modal from './Modal';

const App = () => {
    useEffect(() => {
        document.title="Contact Us Page | Task 1 | Amar Sinha"
    });

    return (
        <>
            <Container>
                <Button />
            </Container>
            <Modal />
        </>
    );
}

const Container = styled.div`
    width:100%;
    height:80vh;
    display:flex;
    align-items:center;
    justify-content:center;
`;

export default App;