import React from "react";
import { NavLink } from 'react-router-dom';

const Header = () => {
    return (
        <>
            <nav className="navbar navbar-light navbar-expand-md" style={{ backgroundColor: "#e3f2fd" }}>
                <div className="container-fluid">
                    <button
                        className="navbar-toggler"
                        type="button"
                        data-bs-toggle="collapse"
                        data-bs-target="#tasksNavbar"
                        aria-controls="tasksNavbar"
                        aria-expanded="false"
                        aria-label="Toggle navigation"
                    >
                        <i className="fas fa-bars"></i>
                    </button>

                    <div
                        className="collapse navbar-collapse"
                        id="tasksNavbar"
                    >
                        <ul className="navbar-nav mb-2 mb-lg-0 m-auto">
                            <li className="nav-item nav-menu-item">
                                <NavLink className="nav-link navigation-link" aria-current="page" to="home">Home</NavLink>
                            </li>
                            <li className="nav-item nav-menu-item">
                                <NavLink className="nav-link navigation-link" to="contact">Task 1</NavLink>
                            </li>
                            <li className="nav-item nav-menu-item">
                                <NavLink className="nav-link navigation-link" to="qna">Task 2</NavLink>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>


        </>
    );
}

export default Header;