import React, { useState } from "react";
import styled from 'styled-components';

const Qusetion = (props) => {

    const { question, name, username, desc, date, upvotes, downvotes, comment } = props.data;

    // description View More Working -------------------------------------
    const [viewMore, setViewMore] = useState(false);
    const viewingMore = () => {
        setViewMore(true);
    }
    // --------------------------------------------------------------------

    // Follow ----------------------------------------------------------------------------- 
    const [following, setFollowing] = useState(false);

    // ------------------------------------------------------------------------------------ 


    // Voting  -----------------------------------------------------------------
    // Down voting
    const [downvote, setDownVote] = useState({
        value: downvotes,
        status: false
    });

    // Up Voting
    const [upvote, setUpvote] = useState({
        value: upvotes,
        status: false
    });



    const downVoter = () => {
        setDownVote({
            value: downvotes + 1,
            status: true
        });
        setUpvote({
            value: upvotes,
            status: false
        });
    }

    const upVoter = () => {
        setUpvote({
            value: upvotes + 1,
            status: true
        });
        setDownVote({
            value: downvotes,
            status: false
        });
    }

    const followSet = () => { 
        setFollowing(!following);
    }
    // -----------------------------------------------------------------------------------

    // For Api data with html tags -----------------------------------------------
    const removeHTML = (str) => {
        var tmp = document.createElement("DIV");
        tmp.innerHTML = str;
        return tmp.textContent || tmp.innerText || "";
    }
    // ---------------------------------------------------------------------------

    // Date --------------------------------------------------------------------------
    const datePrint = new Date(Date.parse(date));
    const options = { year: 'numeric', month: 'long' };
    // -------------------------------------------------------------------------------

    return (
        <>
            <div className="container" style={Container}>
                <div className="row">
                    {/* User Info */}
                    <div className="col-3 col-md-1">
                        <Avatar src="recruiting-monk-tasks/assets/icons/avatar2.png" alt="Avatar" />
                    </div>
                    <div className="col-9 col-md-11">
                        <div className="d-flex" style={{ lineHeight: "1.2" }}>
                            <Username>{name}</Username>
                            &middot;
                            <Follow>
                                {
                                    following ?
                                        <span className="text-secondary" onClick={followSet}>Unfollow</span>
                                        :
                                        <span onClick={followSet}>Follow</span>

                                }
                            </Follow>
                        </div>
                        <div className="d-flex" style={{ lineHeight: "1.2" }}>
                            <UserTitle>{username}</UserTitle>
                            &middot;
                            <DateDisplay>{datePrint.toLocaleDateString("en-us", options)}</DateDisplay>
                        </div>
                    </div>
                </div>
                {/* Question requires no substring method */}
                <Ques>
                    {removeHTML(question)}
                </Ques>

                {/* Description needs to be limited */}
                <Desc>
                    {
                        (desc.length < 340 || viewMore) ?
                            removeHTML(desc)
                            :
                            <div>
                                <span>
                                    {removeHTML(desc.substr(0, 340))}
                                </span>
                                <span onClick={viewingMore} style={{ color: "blue", cursor: "pointer" }}> ... More</span>
                            </div>
                    }
                </Desc>

                <div className="d-flex pt-3 justify-content-between">
                    {/* Voting, Repost, Share, comments toogler options */}
                    <span>
                        <Vote>
                            <button className={`btn focus-remove ${upvote.status ? "text-primary" : ""}`} onClick={upVoter}>
                                <i className="fal fa-arrow-alt-up px-2"></i>
                                {upvote.value}
                            </button>
                            |
                            <button className={`btn focus-remove ${downvote.status ? "text-danger" : ""}`} onClick={downVoter}>
                                <i className="fal fa-arrow-alt-down px-2"></i>
                                {downvote.value}
                            </button>
                        </Vote>

                        <Repost>
                            <button className="btn">
                                <i className="fal fa-sync"></i>
                            </button>
                        </Repost>

                        <Comment>
                            <button className="btn">
                                <i className="fal fa-comment px-2"></i>
                                {comment + 1}
                            </button>
                        </Comment>
                    </span>
                    <span>
                        <Share>
                            <button className="btn">
                                <i className="fal fa-share"></i>
                            </button>
                        </Share>

                        <Options>
                            <button className="btn">
                                <i className="fal fa-ellipsis-h"></i>
                            </button>
                        </Options>
                    </span>
                </div>
                <div className="row">
                    {/* For Adding New Comments */}
                </div>
            </div>
        </>
    );
}

const Container = {
    padding: "0.75em",
    backgroundColor: "#fff"
}

const Avatar = styled.img`
    vertical-align: middle;
    width: 50px;
    height: 50px;
    border-radius: 50%;
`;

const Username = styled.span`
    padding:3px;
    font-size:13px;
    line-height:1.2;
    font-weight:700;
`;

const Follow = styled.span`
    padding:3px;
    font-size:13px;
    font-weight:600;
    line-height:1.2;
    color:#2E69FF;
    cursor:pointer;
`;

const UserTitle = styled.span`
    padding:3px;
    font-size:13px;
    line-height:1.2;
    color:#888;
`;

const DateDisplay = styled.span`
    padding:3px;
    font-size:13px;    
    line-height:1.2;
    color:#888;
`;

const Ques = styled.div`
    padding-top:5px;
    font-weight:700;
`;

const Desc = styled.div`
    padding-top:5px;
    color:#666;
`;

const Vote = styled.span`
    background-color:#f7f7f8;
    border-radius:10px;
    padding:10px;
`;
const Repost = styled.span`

`;
const Comment = styled.span`

`;
const Share = styled.span`

`;
const Options = styled.span`

`;

export default Qusetion;

