import React from "react";
import styled from 'styled-components';
import Answer from "./Answers";
import Question from './Question';


const Post = (props) => {

    const Q = {
        question: props.data.question,
        name: props.data.name,
        username: props.data.username,
        desc: props.data.desc,
        date: props.data.date,
        upvotes: props.data.threshHold.upvotes,
        downvotes:props.data.downvotes.length,
        comment:props.data.answers.length
    }

    const A = {
        answers: props.data.answers
    }


    return (
        <>
            <Container>
                <Question
                    data={Q}
                />
                {
                    (A.answers.length > 0) ?
                        (
                            A.answers.map((answer, i) => {
                                return (
                                    <Answer
                                        key={i}
                                        data={answer}
                                    />
                                )
                            })
                        ) : (
                            <Answer 
                                data={"This is a sample comment. Used here since the api does'nt have an answer for this question."} 
                                upvotes={Q.upvotes}
                                date={"September 15"}
                            />
                        )
                }
            </Container>
        </>
    );
}

const Container = styled.div`
    min-height:40vh;
    width:97.5%;
    margin:20px auto;
    background-color:white;
    border-radius:10px;
    @media(max-width:768px){
    }
    @media(max-width:676px){
        
    }
`;

export default Post;

