import React from 'react';
import { Route, Routes, Navigate } from "react-router-dom";

import Home from './home/App';
import Task1 from './task1/App';
import Task2 from './task2/App';

const Router = () => {
  return (
    <>
      <Routes>
        <Route path="/" element={<Navigate redirect to="home" />} />
        <Route path="/recruiting-monk-tasks/" element={<Home />} />
        <Route path="/home" element={<Home />} />
        <Route path="/contact" element={<Task1 />} />
        <Route path="/qna" element={<Task2 />} />
      </Routes>
    </>
  );
}

export default Router;
