import React, { useState } from "react";
// import styled from 'styled-components';

const Form = () => {
    const [data, setData] = useState({
        fullname: "",
        email: "",
        rating: "0",
        comment: ""
    });

    const InputEvent = (e) => {
        const { name, value } = e.target;

        setData((prev) => {
            return ({
                ...prev,
                [name]: value
            })
        })
    }

    const resetFormData = () => {
        setData(
            {
                fullname: "",
                email: "",
                rating: "0",
                comment: ""

            }
        )
    }

    const formSubmit = (e) => {
        e.preventDefault();
        let errors="";
        // Form Validation 
        if (data.fullname === "") {
            errors+="\n🔹 Name is required cannot be left empty";
        }

        if(data.email===""){
            errors+="\n🔹 Email is required cannot be left empty";
        }else if(!validateEmail(data.email)){
            errors+="\n🔹 Invalid Email ID provided ";
        }

        if(errors!==""){
            alert("Your Form has the following Problems \n" +errors+"");
        }else{
            alert("The Form is Submitted! \n Data Received  \n Name  : "+ data.fullname + " \n Email : " + data.email +" \n Rating : "+ data.rating +" \n Comment : " + data.comment);
        }
    }

    // Email Validation 
    const validateEmail = (emailAdress) => {
        const regexEmail = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
        if (emailAdress.match(regexEmail)) {
            return true;
        } else {
            return false;
        }
    }


    return (
        <>
            <form onSubmit={formSubmit} id="contact-us">
                <div className="mb-3 form-group">
                    <input type="text"
                        className="form-field"
                        id="name"
                        name="fullname"
                        value={data.fullname}
                        onChange={InputEvent}
                        autoComplete="off"
                        placeholder="Enter Full Name"
                    />
                    <label htmlFor="name" className="form-label">Full Name</label>
                </div>
                <div className="mb-3 form-group">
                    <input type="email"
                        className="form-field"
                        id="email"
                        name="email"
                        value={data.email}
                        onChange={InputEvent}
                        autoComplete="off"
                        placeholder="Enter Email Address"
                        aria-describedby="emailHelp"
                    />
                    <label htmlFor="email" className="form-label">Email address</label>
                    <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
                </div>

                <div className="mb-3 form-group">
                    {/* 
                        <div class="form__group field budget-container">
                            <input type="range" min="5000" max="1000000" value="10000" class="budget" name="budget" id='budget' required autocomplete="off" oninput="budget_handling(this)" />
                            <label htmlFor="budget" class="form__label">Your Budget : <span id="budget-value">&#8377; 10000</span></label>
                        </div>
                    */}
                    <input type="range"
                        min={0}
                        max={10}
                        name="rating"
                        autoComplete="off"
                        value={data.rating}
                        onChange={InputEvent}
                        className="rating"
                        id="rating"
                    />
                    <label htmlFor="rating" className="form-label">Rating the Work : {(data.rating > 0) ? (`${data.rating} / 10`) : ("")}</label>
                </div>
                <div className="form-group">
                    <textarea className="form-field"
                        placeholder="Leave a comment here"
                        autoComplete="off"
                        value={data.comment}
                        onChange={InputEvent}
                        name="comment"
                    ></textarea>
                    <label htmlFor="floatingTextarea" className="form-label">Any Comments</label>
                </div>

                <div className="d-flex w-100 justify-content-between p-4">

                    {/* Button to Clear the form */}
                    <button type="reset" className="btn btn-secondary" onClick={resetFormData}>Clear</button>

                    {/* Button to submit the form */}
                    <button type="submit" className="btn btn-primary">Submit</button>

                </div>
            </form>
        </>
    );
}

export default Form;