import React from "react";
import styled from 'styled-components';
import Card from './Card'
import Tasks from './Tasks';

const TaskList = () => {
    return(
    <>
        <Container>
            {
                Tasks.map((task,i)=>{
                    return(
                        <Card
                            key={i}
                            name={task.name}
                            desc={task.description}
                            link={task.link}
                            img={task.screenshot}
                        />
                    );
                })
            }
        </Container>
    </>
    );
}

const Container=styled.main`

`;


export default TaskList;