import React, { useState } from "react";
import styled from 'styled-components';

const Answers = (props) => {
    const answer = props.data;

    const { upvotes, date } = props;


    // answer View More Working -------------------------------------
    const [viewMore, setViewMore] = useState(false);
    const viewingMore = () => {
        setViewMore(true);
    }
    // --------------------------------------------------------------------


    // Up Voting
    const [upvote, setUpvote] = useState({
        value: upvotes,
        status: false
    });


    const upVoter = () => {
        setUpvote({
            value: upvotes + 1,
            status: true
        });
    }


    const removeHTML = (str) => {
        var tmp = document.createElement("DIV");
        tmp.innerHTML = str;
        return tmp.textContent || tmp.innerText || "";
    }

    return (
        <>
            <div className="container" style={Container}>
                <div className="row">
                    {/* User Info */}
                    <div className="col-3 col-md-1">
                        <Avatar src="recruiting-monk-tasks/assets/icons/avatar.png" alt="Avatar" />
                    </div>
                    <div className="col-9 col-md-11">
                        <Username>Andrew Carroll</Username>
                        <Date>{date}</Date>
                    </div>
                </div>
                <Answer>
                    {
                        (answer.length < 340 || viewMore) ?
                            removeHTML(answer)
                            :
                            <div>
                                <span>
                                    {removeHTML(answer.substr(0, 340))}
                                </span>
                                <span onClick={viewingMore} style={{ color: "blue", cursor: "pointer" }}> ... More</span>
                            </div>
                    }
                </Answer>
                <div className="d-flex pt-3 justify-content-between">
                    {/* Voting, Repost, Share, comments toogler options */}

                    <div>
                        <Vote>

                            <button className={`btn ${upvote.status ? "text-primary" : ""}`} onClick={upVoter}>
                                <i className="fal fa-arrow-alt-up px-2"></i>
                                Upvotes &nbsp;&nbsp;
                                {upvote.value}
                            </button>
                        </Vote>

                        <Reply>
                            <button className="btn">
                                Reply  &nbsp;&nbsp;
                                <i className="fal fa-share"></i>
                            </button>
                        </Reply>
                    </div>
                    <div>
                        <Options>
                            <button className="btn">
                                <i className="fal fa-ellipsis-h"></i>
                            </button>
                        </Options>
                    </div>
                </div>
            </div>
        </>
    );
}


const Container = {
    padding: "0.75em",
    backgroundColor: "#F7F7F8",
    border: "#FFF2F2 solid 1px",
    boxShadow: "0 0 2px rgb(0 0 0 / 15%)"
}

const Avatar = styled.img`
    vertical-align: middle;
    width: 50px;
    height: 50px;
    border-radius: 50%;
`;

const Username = styled.div`
    padding:3px;
    font-size:13px;
    line-height:1.2;
    font-weight:700;
`;


const Date = styled.div`
    padding:3px;
    font-size:13px;    
    line-height:1.2;
    color:#888;
`;

const Answer = styled.div`
    padding-top:5px;
    color:#666;
`;

const Vote = styled.span`
    background-color:#fff;
    border-radius:10px;
    padding:10px;
`;

const Reply = styled.span`

`;

const Options = styled.span`

`;

export default Answers;

