import React from "react";
import { NavLink } from 'react-router-dom';
import styled from 'styled-components';

const Card = (props) => {
    return (
        <>
            <Task>
                <div className="row">
                    <Image className="col-sm-6">
                        <NavLink className="nav-link fa-2x" to={props.link}>
                            <img src={props.img} alt={props.name} />
                        </NavLink>
                    </Image>
                    <Desc className="col-sm-6">
                        <div>
                            <NavLink className="nav-link fa-2x" to={props.link}>
                                <div>{props.name}</div>
                            </NavLink>
                            <div>{props.desc}</div>
                        </div>
                    </Desc>
                </div>
            </Task>
        </>
    );
}

const Task = styled.div`
    margin:20px;
    padding:20px 30px;
    background-color:#e3f2fd;
    border-radius:12px;
`;

const Desc = styled.div`
    height:100%;
    width:50%;
    display:flex;
    margin:auto;
    align-items:center;
    justify-content:center;
    @media(max-width:768px){
        width:100%;
    }
`;

const Image = styled.div`
    height 100%;
    width:50%;
    img{
        max-width:100%;
        height:200%;
    }
    @media(max-width:768px){
        width:100%;
    }
`;

export default Card;

